@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800&display=swap");
@import url("https://cdn.jsdelivr.net/npm/@tabler/icons-webfont@latest/tabler-icons.min.css");
@tailwind base;
@tailwind components;
@tailwind utilities;
/*********************************/
/*             General           */
/*===============================*/
html {
  @apply relative scroll-smooth;
}

body {
  @apply font-poppins text-[15px] text-black;
}

h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
  @apply font-poppins leading-normal font-semibold;
}

p {
  @apply leading-relaxed;
  @apply font-poppins;
}

::selection {
  @apply bg-primary/70 text-white;
}

.check-with-label:checked + .label-for-check {
  @apply text-white !important;
}

/*********************************/
/*             Helper            */
/*===============================*/
.container,
.contacinr-fluid {
  @apply relative;
}

.text-type-element {
  @apply z-2 after:content-[""] after:absolute after:h-1 after:w-auto after:bg-orange-300 after:right-0 after:bottom-0 after:left-0 after:z-0 after:rounded-md after:transition-all after:duration-300 after:ease-in-out;
}

@keyframes sk-bounce {
  0%, 100% {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
}